	data() {
		return {
			order:{
				id:-1,
				shipName:'zdh',
				shipMobile:'15077144027',
				shipAddress:'广西壮族自治区南宁市西乡唐区',
				orderitemlist:[
					{
						goodimg:'../../static/logo.png',
						goodName:'测试商品',
						goodNum:2,
						goodPrice:0.02
					},
					{
						goodimg:'../../static/logo.png',
						goodName:'云南白果',
						goodNum:3,
						goodPrice:0.05
					},
					{
						goodimg:'../../static/logo.png',
